import angularApplication from './application';
import getTemplateHTML from './template';

import { ENCODED_QUERY_SET } from '../../../actionTypes';

const transform = (properties = {}) => ({
    table: properties.table,
    tableLabel: properties.tableLabel,
    filterConfig: {
        encodedQuery: properties.encodedQuery,
        queryObj: properties.queryObject,
        sets: !properties.hideSets,
        hideHeader: true,
        saveFilter: false,
        loadFilter: false,
        testFilter: false,
        closeFilter: false,
        clearFilter: false,
        runFilter: false,
        sortFilter: false,
        watchConfig: false,
        dotWalking: !properties.hideDotWalking,
        relatedListQueryConditions: !properties.hideRelatedListQueryConditions,
        fieldList: properties.fieldsWhitelist,
        outputType: properties.outputType,
    },
});

const ROOT_NODE_ID = 'condition-builder';

const getPreloadURL = csrfToken => `AJAXJellyRunner.do?template=conditionBuilderLegacyTemplatePreload.xml&sysparm_ck=${csrfToken}`;

const disableGlobalAngular = window => {
    if (!window.document.body.classList.contains('ng-non-bindable')) {
        window.document.body.classList.add('ng-non-bindable');
    }
};

const jqueryInitialized = typeof window.jQuery === 'undefined';

const cleanUpGlobals = window => {
    const { jQuery } = window;
    if (jQuery) jQuery.noConflict(jqueryInitialized);
};

const appendTemplateCache = ({ document }, element, templateCache) => {
    const child = document
        .createRange()
        .createContextualFragment(templateCache);
    element.querySelector(`.filter-container`).appendChild(child);
};

const angularRenderer = () => {
    let app;
    const SN_CONDITION_BUILDER_LEGACY_RESOURCE =
        'SN_CONDITION_BUILDER_LEGACY_LOADED';
    return {
        onConnect(element, dispatch, next) {
            const loadScripts = !window[SN_CONDITION_BUILDER_LEGACY_RESOURCE];
            const templateHTML = getTemplateHTML(document, loadScripts);
            const scripts = [...templateHTML.querySelectorAll('script')];
            const csrfToken = window.g_ck;
            const promises = [];
            
            if (csrfToken) {
                const url = getPreloadURL(csrfToken);
                const templatePreloadRequest = fetch(url).then(resp => resp.text());
                promises.push(templatePreloadRequest);
            }

            scripts.forEach(
                script => promises.push(new Promise(resolve => (script.onload = resolve)))
            );

            element.appendChild(templateHTML);

            const config = {
                mapBroadcastToDispatch: {
                    'snfilter:update_query': payload => {
                        dispatch(ENCODED_QUERY_SET, {
                            value: payload.query,
                            partial: false
                        });
                    },
                },
            };

            Promise.all(promises).then(([templateCache]) => {
                window[SN_CONDITION_BUILDER_LEGACY_RESOURCE] = true;
                disableGlobalAngular(window);
                cleanUpGlobals(window);
                appendTemplateCache(window, element, templateCache);
                app = angularApplication(
                    element.querySelector(`#${ROOT_NODE_ID}`),
                    window.angular,
                    config
                );
                app.init();
                next();
            });
        },
        onDisconnect() {
            if (app) app.destroy();
        },
        onStateChange(element, viewFn, props = {}) {
            if (!app) return;
            const transformedProps = transform(props.properties);
            app.update(transformedProps);
        },
    };
};

export default angularRenderer;
