import cuid from 'cuid';
import defaultsDeep from 'lodash/defaultsDeep';
import get from 'lodash/get';
import invariant from 'invariant';

const REQUIRES = ['sn.filter_widget', 'sn.ngform', 'sn.common.ui'];

const angularApplication = (context, angular, config = {}) => {
    invariant(angular, 'sn-legacy-condition-builder requires angular js');

    const APPLICATION_ID = cuid();
    const CONTROLLER_ID = cuid();

    let app;
    let injector;

    let controllerInstance = null;

    let isInitialized = false;

    const { mapBroadcastToDispatch = () => {} } = config;

    const bootstrap = () => {
        app = angular.module(APPLICATION_ID, REQUIRES);

        app.config([
            '$controllerProvider',
            $controllerProvider => {
                // This is necessary to create controllers after the app has bootstrapped
                app._controller = app.controller;
                app.controller = function(name, constructor) {
                    $controllerProvider.register(name, constructor);
                    return this;
                };
            },
        ]);

        context.className += ' ng-non-bindable';
        injector = angular.bootstrap(context, [APPLICATION_ID]);
    };
    const createController = () => {
        const elem = context.querySelector('.filter-container');
        elem.setAttribute('data-ng-controller', CONTROLLER_ID);
        elem.setAttribute('id', CONTROLLER_ID);

        const scope = injector.get('$rootScope').$new();
        const controller = function controller($scope) {
            Object.keys(mapBroadcastToDispatch).forEach(key => {
                $scope.$on(key, (evt, query, table) => {
                    mapBroadcastToDispatch[key]({ query, table });
                });
            });
        };
        app.controller(CONTROLLER_ID, ['$scope', controller]);

        controllerInstance = scope;
    };

    const compileController = () => {
        injector.invoke([
            '$compile',
            '$timeout',
            ($compile, $timeout) => {
                const elem = angular.element(
                    context.querySelector(`#${CONTROLLER_ID}`)
                );
                $compile(elem)(controllerInstance);
                $timeout(() => {
                    controllerInstance.$digest();
                });
            },
        ]);
    };

    const update = (scopeProperties, scopeBroadcasts = []) => {
        invariant(controllerInstance, `controllerInstance does not exist`);

        //	A new child scope will get created and assigned within $compile,
        //	$$childHead is a pointer to the actual scope of the controller
        const $scope = controllerInstance.$$childHead;
        const oldQuery = get($scope, "filterConfig.encodedQuery", "");
        const newQuery = get(scopeProperties, "filterConfig.encodedQuery", "");

        //	Update nextProps with any changes to scope fields from within the directive
        defaultsDeep(scopeProperties, $scope);

        //	Forcibly update controller scope properties and run an async $digest
        Object.assign($scope, scopeProperties);

        if (isInitialized) $scope.isInitialized = true;

        setTimeout(() => {
            $scope.$digest();

            //	Broadcast events specified in individual angular directives on "watched" props
            //	This should happen after the scope.$digest
            scopeBroadcasts.forEach(config => {
                const { event, data: payload } = config;
                if ($scope.$root) $scope.$root.$broadcast(event, payload);
            });

            if (oldQuery !== newQuery)
                $scope.$root.$broadcast("snfilter:initialize_query", newQuery);
        });
    };

    const destroy = () => {
        invariant(controllerInstance, `controllerInstance does not exist`);

        let $scope = controllerInstance;
        while ($scope) {
            const nextScope = $scope.$$childHead;
            $scope.$destroy();
            $scope = nextScope;
        }

        controllerInstance = null;
    };

    const init = () => {
        if (isInitialized) return;

        bootstrap();
        createController();
        compileController();

        isInitialized = true;
    };

    return {
        init,
        update,
        destroy,
    };
};

export default angularApplication;
