const template = (includeScripts = true) => `
    <div id="condition-builder">
        <div class="filter-container">
            <sn-filter-widget ng-if="isInitialized" table='{{table}}' table-label='{{tableLabel}}' filter-config="filterConfig"></sn-filter-widget>
        </div>
        ${includeScripts
            ? `<script type="text/javascript" src="/scripts/js_includes_filter_widget_all.js"></script>`
            : ``
        }
    </div>`;

const getTemplateHTML = (doc, includeScripts) =>
    doc.createRange().createContextualFragment(template(includeScripts));
    
export default getTemplateHTML;
