import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import actionHandlers from './actions';
import view from './view';

/**
 * A Condition Builder component that allows users to visually create and edit encoded queries and/or query objects.
 *
 * ```
 * <now-condition-builder-connected
 *      tableName="incident"
 *      tableLabel="Incidents"
 *      encodedQuery="active=true"
 *      hideSets=false
 *      hideDotWalking=false
 *      hideRelatedListQueryConditions=false />
 * ```
 *
 * @seismicElement now-condition-builder
 * @summary A Seismic Condition Builder component that allows users to visually create and edit encoded queries and/or query objects.
 */
createCustomElement('now-condition-builder', {
    renderer: { type: snabbdom },
    view,
    properties: {
        /**
         * Initial encoded query to be rendered by condition blocks. Will be updated as conditions are edited in condition blocks.
         * Required: No
         *
         * @type {string}
         */
        encodedQuery: {
            default: '',
        },
        /**
         * A query object may be passed and received instead of an encoded query.
         * Required: No
         *
         * @type {object}
         */
        queryObject: {},
        /**
         * The name of the table from which the conditions will be rendered.
         * Required: Yes
         *
         * @type {string}
         */
        tableName: {
            default: '',
        },
        /**
         * Customer friendly name of the table from which the conditions will be rendered.
         * Required: Yes
         *
         * @type {string}
         */
        tableLabel: {
            default: '',
        },
        /**
         * Hide the ability to create sets.
         * Required: No
         *
         * @type {boolean}
         */
        hideSets: {
            default: false,
        },
        /**
         * Hide the ability to dot walk from any fields.
         * Required: No
         *
         * @type {boolean}
         */
        hideDotWalking: {
            default: false,
        },
        /**
         * Hide the ability to create related list queries.
         * Required: No
         *
         * @type {boolean}
         */
        hideRelatedListQueryConditions: {
            default: false,
        },
        watchConfig: {
            default: true,
        },
        /**
         * Select desired output type that will be received when the query is updated.
         * Available options: encoded_query, query_object
         * Required: No
         *
         * @type {string}
         */
        outputType: {
            default: 'encoded_query',
        },
        /**
         * Whitelist of fields to display in dot walk components, comma delimiter
         * Required: No
         *
         * @type {[string]}
         */
        fieldsWhitelist: {
            default: [],
        },
        /**
         * Show/Hide the ability to save a filter
         * Required: No
         *
         * @type {boolean}
         */
        hideSaveFilter: {
            default: false,
            schema: {
                type: 'boolean',
            },
        },
        /**
         * Show/Hide the ability to load a filter
         * Required: No
         *
         * @type {boolean}
         */
        hideLoadFilter: {
            default: false,
            schema: {
                type: 'boolean',
            },
        },
        /**
         * Show/Hide the filter overview for the current encoded query
         * Required: No
         *
         * @type {boolean}
         */
        hideFilterOverview: { default: false, schema: { type: 'boolean' } },

        /**
         * Render custom element in place of the default comparison row
         * Required: No
         *
         * @type {string}
         */
        comparisonRowElement: {
            default: undefined,
            schema: {
                type: 'string',
            },
        },

        /**
         * Array of current user's roles
         * Required: No
         *
         * @type {[string]}
         */
        userRoles: {
            default: [],
            schema: {
                type: 'array',
                items: {
                    type: 'string',
                },
            },
        },
    },
    actionHandlers,
});
