import { createCustomElement } from '@servicenow/ui-core';
import rtlBehavior from '@servicenow/behavior-rtl';
import actionHandlers from './actions';
import angularRenderer from './angular/renderer';
import styles from './conditionBuilder.scss';
import get from 'lodash/get';

createCustomElement('sn-legacy-condition-builder', {
    actionHandlers,
    eventHandlers: [
        {
            events: ['click'],
            effect(coeffects) {
                const {
                    updateState,
                    action: {
                        payload: {
                            event: { path },
                        },
                    },
                } = coeffects;

                const currentPath =
                    path || (event.composedPath && event.composedPath());
                
                    const triggerButton = currentPath.filter(
                    node =>
                        node.classList &&
                        node.classList.contains('sn-popover-complex')
                );

                const pathContainsPopover = currentPath.some(
                    node => node.classList && node.classList.contains('popover')
                );

                if (pathContainsPopover) {
                    return;
                }

                const eventManager = get(window, 'NOW.CustomEvent');
                if (eventManager && typeof eventManager.fire === "function") {
                    eventManager.fire('fieldListCloseEvent', {}, { target: triggerButton })
                }
            },
            target: document,
        },
    ],
    renderer: {
        type: angularRenderer(),
    },
    view() {},
    initialState: {
        closeEvent: {},
    },
    properties: {
        encodedQuery: {
            default: '',
        },
        queryObject: {},
        table: {
            default: '',
        },
        tableLabel: {
            default: '',
        },
        hideSets: {
            default: false,
        },
        hideDotWalking: {
            default: false,
        },
        hideRelatedListQueryConditions: {
            default: true,
        },
        outputType: {
            default: 'encoded_query',
        },
        fieldsWhitelist: {
            default: [],
        },
    },
    behaviors: [
        {
            behavior: rtlBehavior,
        },
    ],
    styles,
});
