import '../sn-legacy-condition-builder';
import '@servicenow/now-loader';

const view = ({ properties }) => {
    const {
        dir,
        tableName,
        tableLabel,
        encodedQuery,
        hideSets,
        hideDotWalking,
        hideRelatedListQueryConditions,
        fieldsWhitelist,
    } = properties;

    return (
        <sn-legacy-condition-builder
            table={tableName}
            encodedQuery={encodedQuery}
            tableLabel={tableLabel}
            hideSets={hideSets}
            hideDotWalking={hideDotWalking}
            hideRelatedListQueryConditions={hideRelatedListQueryConditions}
            fieldsWhitelist={fieldsWhitelist}
            dir={dir}
        />
    );
};

export default view;
